























































































































































































































































/**
 * name: HouseRoomTypeInfo
 * description:
 * author: roct
 * date: 18:28 2021/4/17 0017
 */
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import {
  getRoomTypeList,
  getDictSelect,
  saveRoomType,
  updateRoomType
} from '@/api/manage/sys/housePageApi'
interface FormObjType {
   id?: string
    area?: string
    decorateType?: string
    fileList?: {
        url: string
      }[], // 文件图片地址
    hall?: string, // 几厅
    roomCount?: string, // 几室
    who?: string, // 几卫
    intro?: string, // 房型介绍
    name?: string, // 房型名称
    price?: string, // 价格
    projectId?: string, // 对应的项目id
    rentType?: string, // 租赁方式
    status?: string, // 状态, 0是禁用, 1是启动
    roomTypeConfigList: {
        // 房型配置类型
        count: string, // 数量
        projectId: string,
        remark: string, //  备注
        roomItem: string, // 选择的物品名称, 比如床等等
        roomTypeId: string, // 选择的物品的id
        status: string // 状态, 0是禁用, 1是启动
      }[]
}
@Component({
  components: {}
})
export default class HouseRoomTypeInfo extends Vue {
  @Prop({ type: [Number, String], default: '' })
  private projectId!: number | string

  // 类型列表
  private roomTypeList: any[] = []
  // btn loading
  private submitLoading = false
  // 记录当前点击的第几个
  private currentIndex = -1
  // 表单数据, 现在正在操作的
  private formObj: FormObjType = {
    id: '', // 当前的id
    area: '', // 面积
    decorateType: '', // 装修类型
    fileList: [
      {
        url: ''
      }
    ], // 文件图片地址
    hall: '', // 几厅
    roomCount: '', // 几室
    who: '', // 几卫
    intro: '', // 房型介绍
    name: '', // 房型名称
    price: '', // 价格
    projectId: '', // 对应的项目id
    rentType: '', // 租赁方式
    status: '1', // 状态, 0是禁用, 1是启动
    roomTypeConfigList: []
  }
  // 表单数据, 初始化的
  private formObjInit: FormObjType = {
    area: '', // 面积
    decorateType: '', // 装修类型
    fileList: [
      {
        url: ''
      }
    ], // 文件图片地址
    hall: '', // 几厅
    roomCount: '', // 几室
    who: '', // 几卫
    intro: '', // 房型介绍
    name: '', // 房型名称
    price: '', // 价格
    projectId: '', // 对应的项目id
    rentType: '', // 租赁方式
    status: '1', // 状态, 0是禁用, 1是启动
    roomTypeConfigList: [
      
    ]
  }
  // 处理本地图片列表
  private imgsUrl: any[] = []
  // 装修列表
  private decorate_type: any[] = []
  // 房型物品配置列表
  private room_type_config: any[] = []
  // 租赁方式
  private rent_type: any[] = []
  public mounted() {
    this.getRoomTypeList()
    this.typeList()
  }
  // 获取上传接口的域名
  get baseUrl() {
    return process.env.VUE_APP_URL
  }
  /**
   * @Author roct
   * @Description 获取下拉列表等信息
   *
   * @Date 22:42 2021/4/17 0017
   **/
  private async typeList() {
    // 获取装修列表
    const res = await getDictSelect({ dictCode: 'decorate_type' })
    this.decorate_type = res.data
    // 房型物品配置列表
    const res_room = await getDictSelect({ dictCode: 'room_type_config' })
    this.room_type_config = res_room.data
    // 租赁方式
    const res_rent_type = await getDictSelect({ dictCode: 'rent_type' })
    this.rent_type = res_rent_type.data
  }

  /**
   * @Author roct
   * @Description 获取房型列表
   * @Date 19:43 2021/4/17 0017
   **/
  private async getRoomTypeList() {
    try {
      const res = await getRoomTypeList({ projectId: this.projectId })
      if (res.code === 200) {
        this.roomTypeList = res.data
        if (this.roomTypeList.length > 0) {
          this.clickRoomType(this.roomTypeList[0], 0, true)
        }
      } else {
        this.$message.error(res.msg + '')
      }
    } catch (e) {
      this.$message.error(e.msg || e.message)
    }
  }
  /**
   * @Author roct
   * @Description提交表单
   * @Date 21:32 2021/4/17 0017
   **/
  private submitForm(ruleForm: string) {
    this.requestSubmitPost()
  }
  /**
   * @Author roct
   * @Description 提交数据的request
   * @Date 21:33 2021/4/17 0017
   **/
  private async requestSubmitPost() {
    this.submitLoading = true
    this.formObj.projectId = this.projectId + ''
    for (const item of this.formObj.roomTypeConfigList) {
      item.projectId = this.projectId + ''
    }
    if (
      !this.formObj.name ||
      !this.formObj.projectId ||
      !this.formObj.hall ||
      !this.formObj.roomCount ||
      !this.formObj.area ||
      !this.formObj.decorateType
    ) {
      this.$message.warning('请输入完整的数据')
      return
    }
    try {
      let res: {
        code: number
        data: any
        msg: string
      }
      if (!this.formObj.id) {
        res = await saveRoomType(this.formObj)
      } else {
        res = await updateRoomType(this.formObj)
      }
      if (res.code === 200) {
        this.formObj.id = res.data
        this.imgsUrl = []
        await this.getRoomTypeList()
        await this.typeList()
        this.$message.success('保存成功')
      } else {
        this.$message.error(res.msg)
      }
      this.submitLoading = false
    } catch (e) {
      this.$message.error(e.msg || e.message)
      this.submitLoading = false
    }
  }

  /**
   * @Author roct
   * @Description 删除物品列表
   * @Date 19:42 2021/4/17 0017
   **/
  private delRoomConfigInfoClick(scope: any) {
    this.formObj.roomTypeConfigList.splice(scope.$index, 1)
  }
  /**
   * @Author roct
   * @Description 物品列表新增
   * @Date 19:43 2021/4/17 0017
   **/
  private addRoomConfigInfoClick(scope: any) {
    const item = this.room_type_config.find((target: any) => {
      return target.value === scope.row.roomItem
    })
    if (item) {
      this.formObj.roomTypeConfigList.push({
        // 房型配置类型
        count: scope.row.count, // 数量
        projectId: '',
        remark: scope.row.remark, //  备注
        roomItem: item.name, // 选择的物品名称, 比如床等等
        roomTypeId: scope.row.roomItem, // 选择的物品的id
        status: '1' // 状态, 0是禁用, 1是启动
      })
    }
  }
  /**
   * @Author roct
   * @Description 添加房屋类型
   * @Date 20:58 2021/4/17 0017
   **/
  private addRoomTypeClick() {
    if (this.currentIndex >= 0) {
      this.roomTypeList[this.currentIndex] = JSON.parse(
        JSON.stringify(this.formObj)
      )
    }
    const copy = JSON.parse(JSON.stringify(this.formObjInit))
    this.formObj = copy
    this.currentIndex = -1
    this.imgsUrl = []
  }
  /**
   * @Author roct
   * @Description 切换房屋类型
   * @Date 21:17 2021/4/17 0017
   * @return
   **/
  private clickRoomType(item: any, index: number, refresh = false) {
    if (index === this.currentIndex && !refresh) {
      return
    }
    if (this.currentIndex >= 0) {
      this.roomTypeList[this.currentIndex] = JSON.parse(
        JSON.stringify(this.formObj)
      )
    }
    this.currentIndex = index
    this.formObj = JSON.parse(JSON.stringify(item))
    if (this.formObj.fileList) {
      this.imgsUrl =
            this.formObj.fileList.length === 1 && this.formObj.fileList[0].url === ''
              ? []
              : JSON.parse(JSON.stringify(this.formObj.fileList))
      this.giveImageUrlsAddDomian()
    }
  }
  /**
   * @Author roct
   * @Description 给图片列表增加域名
   * @Date 14:26 2021/4/18 0018
   **/
  private giveImageUrlsAddDomian() {
    for (const item of this.imgsUrl) {
      if (item.url.indexOf('http:') === -1 && item.url.indexOf('https:') === -1) {
        item.url = 'http://sayhi-room.oss-cn-qingdao.aliyuncs.com/' + item.url
      }
    }
  }
  /**
   * 上传图片出错
   */
  private handleExceed(e: any) {
    this.$message.warning('上传图片超出数量')
  }
  /**
   * 移除照片
   */
  private handleRemove(file: any, fileList: any[]) {
    this.formObj.fileList = []
    for (const item of fileList) {
      this.formObj.fileList.push({
        url: item.response.data
      })
    }
  }
  /**
   * 上传成功
   */
  private uploadSuccess(response: any, file: any, fileList: any[]) {
    if (this.formObj.fileList) {
      if (
      this.formObj.fileList.length === 1 &&
      this.formObj.fileList[0].url === ''
      ) {
        this.formObj.fileList = []
      }
      this.formObj.fileList.push({
        url: file.response.data
      })
    }
   
  }

  private uploadError(err: any, file: any, fileList: any) {
    this.$message.error('上传出错, 请删除后继续')
  }
}
